/* global document, Office */

Office.onReady((info) => {
    if (info.host === Office.HostType.Outlook) {
        // extractUrlFromEmail();
        populateUTMDefaults();
        //document.getElementById("run").onclick = EditSignature;
        document.getElementById("Update").onclick = generateFinalUrl;
        //document.getElementById("sync").onclick = Sync;

        const email = Office.context.mailbox.item
        //email.body.prependAsync(` ${email.addHandlerAsync} `)
        email.addHandlerAsync(Office.EventType.RecipientsChanged, function () {
            populateUTMDefaults();
        })
    }
});


async function Sync() {
    const item = Office.context.mailbox.item;

    // Email validation regex
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

    // Synchronize the subject with UTM Content
    item.subject.getAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
            const currentSubject = result.value || "";
            const subjectInput = document.getElementById("utm_content").value || "";

            if (currentSubject !== subjectInput) {
                if (subjectInput.trim() === "blank" || subjectInput.trim() === "") {
                    document.getElementById("utm_content").value = currentSubject;
                    console.log("UTM Content input updated to match the email subject.");
                } else {
                    item.subject.setAsync(subjectInput, (updateResult) => {
                        if (updateResult.status === Office.AsyncResultStatus.Succeeded) {
                            console.log("Email subject updated to match UTM Content input.");
                        } else {
                            console.error("Error updating subject:", updateResult.error);
                        }
                    });
                }
            }
        } else {
            console.error("Error retrieving subject:", result.error);
        }
    });

    // Synchronize the "To" recipients with UTM Campaign
    item.to.getAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
            const toRecipients = result.value.map((recipient) => recipient.emailAddress).join(", ");
            const toInput = document.getElementById("utm_campaign").value || "";

            if (toRecipients !== toInput) {
                if (toInput.trim() === "") {
                    document.getElementById("utm_campaign").value = toRecipients;
                    console.log("UTM Campaign input updated to match To recipients.");
                } else {
                    const validEmails = toInput.split(",").filter((email) => emailRegex.test(email.trim()));
                    if (validEmails.length > 0) {
                        const newRecipients = validEmails.map((email) => ({ emailAddress: email.trim() }));
                        item.to.setAsync(newRecipients, (updateResult) => {
                            if (updateResult.status === Office.AsyncResultStatus.Succeeded) {
                                console.log("To recipients updated to match UTM Campaign input.");
                            } else {
                                console.error("Error updating To recipients:", updateResult.error);
                            }
                        });
                    } else {
                        console.log("No valid email addresses found in UTM Campaign input.");
                    }
                }
            }
        } else {
            console.error("Error retrieving To recipients:", result.error);
        }
    });

    // Synchronize the "CC" recipients with UTM Term
    item.cc.getAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
            const ccRecipients = result.value.map((recipient) => recipient.emailAddress).join(", ");
            const ccInput = document.getElementById("utm_term").value || "";

            if (ccRecipients !== ccInput) {
                if (ccInput.trim() === "") {
                    document.getElementById("utm_term").value = ccRecipients;
                    console.log("UTM Term input updated to match CC recipients.");
                } else {
                    const validEmails = ccInput.split(",").filter((email) => emailRegex.test(email.trim()));
                    if (validEmails.length > 0) {
                        const newRecipients = validEmails.map((email) => ({ emailAddress: email.trim() }));
                        item.cc.setAsync(newRecipients, (updateResult) => {
                            if (updateResult.status === Office.AsyncResultStatus.Succeeded) {
                                console.log("CC recipients updated to match UTM Term input.");
                            } else {
                                console.error("Error updating CC recipients:", updateResult.error);
                            }
                        });
                    } else {
                        console.log("No valid email addresses found in UTM Term input.");
                    }
                }
            }
        } else {
            console.error("Error retrieving CC recipients:", result.error);
        }
    });

    // Add further synchronization logic for additional fields if required.
}




let dialog; // Declare dialog as global for use in later functions.
let LocalUrl = "https://localhost:3000/assets/edit-signature.html";
let LiveUrl = "https://junaid042.github.io/utm-manager/assets/edit-signature.html";

export async function EditSignature() {
    const signatureHtml = localStorage.getItem("signatureHtml") || ""; // Get HTML from localStorage

    Office.context.ui.displayDialogAsync(LiveUrl, { height: 40, width: 70, promptBeforeOpen: false },
        (asyncResult) => {
            dialog = asyncResult.value;
            console.log(asyncResult);

            // Send the signature HTML to the dialog
            dialog.addEventHandler(Office.EventType.DialogMessageReceived, processMessage);
            dialog.addEventHandler(Office.EventType.DialogEventReceived, handleDialogClosed);

            // Wait a short time to ensure the dialog is fully loaded
            setTimeout(() => {
                dialog.messageChild(signatureHtml);
                console.log("html sent to dialog");
            }, 5000); // Adjust the delay as needed
        }
    );
}

function handleDialogClosed() {
    console.log("Dialog closed by user.");
}

function processMessage(arg) {
    // Close the dialog
    dialog.close();

    // Parse the received message
    const messageFromDialog = arg.message;

    localStorage.setItem("signatureHtml", messageFromDialog);
}


// Helper function to format the current date and time
function getCurrentDateTime() {
    const now = new Date();
    const year = now.getFullYear().toString().slice(-2); // Last two digits of the year
    const month = (now.getMonth() + 1).toString().padStart(2, "0");
    const day = now.getDate().toString().padStart(2, "0");
    const hours = now.getHours().toString().padStart(2, "0");
    const minutes = now.getMinutes().toString().padStart(2, "0");
    return `${day}-${month}-${year}_${hours}.${minutes}`;
}

async function populateUTMDefaults() {
    try {
        const item = Office.context.mailbox.item;

        // Get the "To" recipients
        const toRecipients = await new Promise((resolve, reject) => {
            item.to.getAsync((result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });

        // Get the "CC" recipients
        const ccRecipients = await new Promise((resolve, reject) => {
            item.cc.getAsync((result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });

        // Get the email subject
        const subject = await new Promise((resolve, reject) => {
            item.subject.getAsync((result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });

        // Extract recipient addresses
        const toAddresses = toRecipients.map((recipient) => recipient.emailAddress).join(", ");
        const ccAddresses = ccRecipients.length > 0
            ? ccRecipients.map((recipient) => recipient.emailAddress).join(", ")
            : "blank";

        // Generate Medium with current datetime
        const medium = (`e-mail_${getCurrentDateTime()}`).replace(/ /g, "_");

        // Populate the UTM fields
        document.getElementById("utm_campaign").value = toAddresses;
        document.getElementById("utm_source").value = Office.context.mailbox.userProfile.emailAddress;
        document.getElementById("utm_medium").value = medium;
        document.getElementById("utm_content").value = subject || "blank";
        document.getElementById("utm_term").value = ccAddresses;

    } catch (error) {
        console.error("Error populating UTM defaults:", error);
    }
}


let bodyhtml;

function Getbodyhtml(sendhtmlback) {
    try {
        Office.context.mailbox.item.body.getAsync(
            "html",
            { asyncContext: "This is passed to the callback" },
            function callback(result) {
                // console.log(bodyhtml);
                sendhtmlback(null, result.value)
            });
    } catch (err) {
        console.log(err);
    }
}

